const spent = (state = '', action) => {
    switch (action.type) {
        case 'Health':
            return '';
        case 'Food':
            return '';
        case 'Hygiena':
            return '';
        case 'Home':
            return '';
        case 'Clothes':
            return '';
        case 'Sport':
            return '';
        case 'Resort':
            return '';
        case 'Mobile':
            return '';
        case 'Transport':
            return '';
        case 'Animals':
            return '';
        case 'Gifts':
            return '';
        case 'Other':
            return '';
        case 'All':
            return '';
        default:
            return state
    }
};

export default spent;


